var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"reserve"},[_c('div',{staticClass:"article"},[_c('p',[_vm._v("\n      广西雅长兰科植物自然保护区成立于2005年4月，2009年9月经国务院批准晋升为国家级保护区，是我国唯一一个以兰科植物命名并以其为主要保护对象的自然保护区，总面积22062\n      公顷，地处广西西北部的百色市乐业县，位于云贵高原边缘，红水河上游的南盘江畔。主要保护对象为野生兰科植物资源、南亚热带典型植被类型及森林生态系统、细叶云南松林、龙滩水库水源涵养林等。\n    ")]),_c('p',[_vm._v("\n      广西雅长兰科植物国家级自然保护区管理中心属广西壮族自治区林业局直属的财政全额拨款参公管理事业单位，机构规格相当正处级，管理中心设置办公室、监察科、计划财务科、资源保护科（安全防火办）、宣传教育科、科研监测科（兰花繁育实验中心）、社区服务科7个职能科室及3个管理站12个管理点，目前在职在编职工100人。\n    ")]),_c('div',[_c('img',{attrs:{"src":require("../../../assets/phone/AppVisitorHome/image1.png"),"alt":""}})]),_c('p',[_vm._v("\n      地理位置：保护区地处云贵高原东南边缘（东经106°11′31″～106°27′4″，北纬24°44′16″～24°53′58″之间），位于广西壮族自治区西北部乐业县境内，地跨乐业县花坪、雅长和逻沙3个乡镇9个行政村。保护区东西长26.2km，南北宽18km，总面积33万亩。\n    ")]),_c('div',[_c('img',{attrs:{"src":require("../../../assets/phone/AppVisitorHome/image5.jpg"),"alt":""}})]),_c('p',[_vm._v("\n      地质地貌：保护区所处大地质构造属于华南准地台右江再生地槽桂西拗陷西林区――百色断裂带东侧。主要地貌类型为中山地貌和低山地貌。最高海拔1971m，最低海拔400m，相对高差1571m，各山体之间沟谷纵横，叠峰连绵。\n    ")]),_c('div',[_c('img',{attrs:{"src":require("../../../assets/phone/AppVisitorHome/image3.png"),"alt":""}})]),_c('p',[_vm._v("\n      气候条件：保护区地处桂西中亚热带季风气候区，深受季风环流和焚风效应的影响，夏季盛行海洋湿润气团，冬季盛行大陆寒冷气团。年平均气温16.3℃；年平均日照时数变动在1303.7-1698.7h之间；年平均降雨量在940.8-1216.9mm范围内。空气中负离子含量为1-3万个/m3，是典型的天然氧吧，气候温和、夏无酷暑、冬无严寒。\n    ")]),_c('div',[_c('img',{attrs:{"src":require("../../../assets/phone/AppVisitorHome/image2.png"),"alt":""}})]),_c('p',[_vm._v("森林资源：保护区森林面积19755.95公顷，森林覆盖率为88.79%。")]),_c('p',[_vm._v("\n      兰科植物资源：保护区野生兰科植物现分布有64属173种，约占广西兰科植物总种数三分之一，位居广西第一。其中莎叶兰、带叶兜兰和台湾香荚兰野生居群是目前全球最大的野生居群，是世界野生兰科植物的重要分布区及基因库，被授予“中国兰花之乡”称号。\n    ")]),_c('div',[_c('img',{attrs:{"src":require("../../../assets/phone/AppVisitorHome/image6.jpg"),"alt":""}})]),_c('p',[_vm._v("\n      植物资源：保护区维管束植物201科918属2079种（含变种、亚种、栽培变种和变型，下同），其中蕨类植物30科75属150种，裸子植物10科16属28种，被子植物167科870属2254种。其中属Ⅰ级保护的有长瓣兜兰、小叶红豆和叉孢苏铁3种，Ⅱ级保护的有金毛狗蕨、桫椤、黄杉、福建柏、翠柏、鹅掌楸、蒜头果、榉树、红椿、掌叶木等65种。\n    ")]),_c('div',[_c('img',{attrs:{"src":require("../../../assets/phone/AppVisitorHome/image8.jpg"),"alt":""}})]),_c('p',[_vm._v("\n      大型真菌：保护区已知大型真菌共有182种，隶属82属40科。其中担子菌162种，子囊菌20种；可供食用的大型真菌76种，药用菌43种，毒菌12种，腐生菌35种。\n    ")]),_c('div',[_c('img',{attrs:{"src":require("../../../assets/phone/AppVisitorHome/image9.jpg"),"alt":""}})]),_c('p',[_vm._v("\n      野生动物资源：保护区目前已知的陆生脊椎动物共有4纲28目91科320种，其中两栖纲1目5科18种，爬行纲3目12科42种，鸟纲15目52科206种，哺乳纲9目22科54种。有国家重点保护动物42种。其中：国家Ⅰ级保护动物5种：金钱豹、林麝、金雕、黑颈长尾雉、蟒蛇。国家Ⅱ级保护动物有虎纹蛙、山瑞鳖、地龟、蛤蚧、黑冠鹃隼等37种。\n    ")]),_c('div',[_c('img',{attrs:{"src":require("../../../assets/phone/AppVisitorHome/image11.png"),"alt":""}})]),_c('p',[_vm._v("\n      昆虫资源：保护区昆虫种类繁多，资源丰富，已鉴定学名的昆虫12目99科509\n      种，其中有新种1个。根据其作用和价值，分为传粉昆虫、药用昆虫、观赏昆虫、天敌昆虫、食用昆虫。\n    ")]),_c('div',[_c('img',{attrs:{"src":require("../../../assets/phone/AppVisitorHome/image10.jpg"),"alt":""}})])])])}]

